














































import { Vue, Component, Prop, Provide, Emit, Inject, Mixins } from 'vue-property-decorator'
import { genBgImg, genPosX, genPosY, genSize, getStrValue, genPosition } from '../core/utils'
import { positionType } from '../core/constants'

const GIFT = 'data:image/png;base64,'
const BG = 'data:image/png;base64,'

@Component
export default class HPromoCode extends Vue {
  @Prop({ type: String, default: 'relative' })
  position: positionType

  @Prop({ type: [Number, String], default: '100%' })
  width!: string | number

  @Prop({ type: [Number, String], default: 100 })
  height!: string | number

  @Prop({ type: [Number, String], default: 0 })
  x!: string | number

  @Prop({ type: [Number, String], default: 0 })
  y!: string | number

  @Prop({ type: String, default: 'Code' })
  label: string

  @Prop({ type: String, default: '10086' })
  code: string

  @Prop({
    type: String,
    default: GIFT
  })
  gift: ImageData

  @Prop({
    type: String,
    default: BG
  })
  bgImg: ImageData

  get viewLabel () {
    return getStrValue(this, this.label)
  }

  get viewCode () {
    return getStrValue(this, this.code)
  }

  get float (): boolean {
    return this.position !== 'relative'
  }

  get styles (): any {
    const { bgImg, x, y, height, width, float, position } = this
    const styles = {}

    genPosition(styles, position)
    genBgImg(styles, bgImg)
    genPosX(styles, x, float)
    genPosY(styles, y, float)
    genSize(styles, 'height', height)
    genSize(styles, 'width', width)

    return styles
  }
}
